@use "../abstract" as *;

.nav {
  display: none;

  &--active {
    display: block;
  }

  @include mq(md) {
    display: block;
  }

  &__list {
    display: flex;
    list-style: none;
    padding: 0;
    flex-direction: column;
    align-items: center;

    @include mq(md) {
      flex-direction: row;
      align-items: center;
    }
  }

  &__item {
    @include mq(md) {
      & + & {
        // margin-left: var(--sp-md);
        margin-left: 4em;
      }
    }
  }

  &__link {
    color: inherit;
    text-decoration: none;
    font-family: var(--ff-sf-pro-text-regular);
    font-size: pxToRem(14);
    transition: color 0.2s ease-in-out;

    &:hover {
      color: var(--clr-primary-300);
    }

    &:active {
      color: var(--clr-primary-400);
    }
  }
}
