@use "../abstract" as *;

.header {
  background-color: var(--clr-primary-100);
  box-shadow: 1px 0px 20px var(--clr-primary-600);
  position: fixed;
  width: 100%;
  z-index: 999;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 1em;

  @include mq(md) {
    display: block;
    padding-block: 0;
  }

  &__logo {
    color: inherit;
    text-decoration: none;
    font-family: var(--ff-poppins-bold);
    display: grid;
    place-items: center;
    font-size: pxToRem(24);
  }

  &__content {
    position: relative;
  }

  &__dropdown {
    display: none;
    overflow: hidden;
    width: 100%;
    text-align: center;

    &--active {
      display: block;
    }

    @include mq(md) {
      display: block;
      max-width: 4em;
      text-align: left;
      &:hover .header__dropdown-content {
        display: block;
      }
    }
  }

  &__dropbtn {
    display: none;

    @include mq(md) {
      display: flex;
      justify-content: space-around;
      align-items: baseline;
      font-family: var(--ff-poppins-regular);
      font-size: pxToRem(14);
      color: var(--clr-primary-200);
      border: none;
      cursor: pointer;
      background: transparent;
      width: 100%;
    }
  }

  &__dropdown-content {
    display: block;
    background-color: transparent;

    @include mq(md) {
      display: none;
      position: absolute;
      min-width: 100px;
      box-shadow: 1px 0px 20px var(--clr-primary-600);
      background-color: var(--clr-primary-200);
      z-index: 1000;
    }
  }

  &__dropdown-content a {
    color: inherit;
    display: block;
    padding: 0;
    text-decoration: none;
    font-family: var(--ff-poppins-regular);
    font-size: pxToRem(14);

    @include mq(md) {
      padding: 0.75em 1em;
      color: var(--clr-primary-100);
      &:hover {
        background-color: var(--clr-primary-100);
        color: var(--clr-primary-200);
      }
    }
  }

  &__hamburger {
    display: inline-block;
    border: none;
    background: transparent;
    position: absolute;
    margin-top: 0.25em;
    top: 0;
    left: 0;

    @include mq(md) {
      display: none;
    }
  }

  &__icon {
    color: var(--clr-primary-200);
    font-size: pxToRem(24);
  }
}
