@use "../abstract" as *;

%section-heading {
  font-family: var(--ff-poppins-light);
  font-size: 2rem;
  line-height: 1.2;
  color: var(--clr-primary-100) !important;

  @include mq(md) {
    font-size: 3rem;
    line-height: 1.5;
  }
}

%section-description {
  font-family: var(--ff-sf-pro-text-regular);
  font-size: pxToRem(14);
  line-height: 1.2;
  margin-top: 1em;

  @include mq(md) {
    font-size: pxToRem(20);
    line-height: 1.5;
    margin-top: 0.7em;
  }
}

.banner {
  background-image: url("../assets/images/banner.svg");
  background-size: contain;
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  // TODO: Add color in the colors palette
  background-color: #000000c9;
  display: grid;
  place-items: center;
  background-attachment: fixed;

  @include mq(md) {
    background-position: bottom;
  }

  &__content {
    display: grid;
    place-items: center;
    text-align: center;
    padding-block: 2em;
    padding-inline: 1em;

    @include mq(md) {
      // TODO: Add spacing in the spacing constant
      max-width: 45em;
    }
  }

  &__heading {
    font-family: var(--ff-poppins-medium);
    font-size: 2rem;
    color: var(--clr-primary-300);
    line-height: 1;

    @include mq(md) {
      font-size: 4rem;
    }
  }

  &__description {
    font-family: var(--ff-sf-pro-text-regular);
    font-size: pxToRem(12);
    color: var(--clr-primary-200);
    // width: 65%;
    line-height: 1.5;
    letter-spacing: 0.01em;
    // TODO: Add spacing in the spacing constant
    margin-block: 1em;

    @include mq(md) {
      font-size: pxToRem(21);
    }
  }
}

.about {
  background-color: var(--clr-primary-200);
  background-image: url("../assets/images/about.png");
  min-height: 90vh;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: contain;
  background-attachment: fixed;
  display: grid;
  justify-content: center;
  align-content: baseline;
  padding-block: 2em; // TODO: Add spacing in the spacing constant

  @include mq(md) {
    padding-block: 3.75em;
    min-height: 100vh;
  }

  &__content {
    display: grid;
    place-items: center;
    text-align: center;

    @include mq(md) {
      // TODO: Add spacing in the spacing constant
      max-width: 45em;
    }
  }

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-400);
  }

  &__description {
    @extend %section-description;
    color: var(--clr-primary-500);
    width: 81%;
  }
}

.features {
  background-color: #fff;
  // TODO: Add spacing in the spacing constant
  padding-bottom: 2.75em;
  padding-top: 1.5em;

  @include mq(md) {
    padding-bottom: 3.75em;
  }

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-300);
    text-align: center;
  }

  &__description {
    @extend %section-description;
    color: var(--clr-primary-500);
    text-align: center;
    margin-bottom: 1em;

    @include mq(md) {
      margin-bottom: 0;
    }
  }

  &__content {
    @include mq(md) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 3em;
      row-gap: 2em;
      margin-top: 3.75em;
    }
  }
}

.choose {
  // TODO: Add spacing in the spacing constant
  padding-bottom: 3.75em;
  padding-top: 2em;

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-400);
    text-align: center;
  }

  &__content {
    margin-top: 2em;

    @include mq(md) {
      display: grid !important;
      grid-template-columns: 1fr 1fr;
      gap: 1em;
    }
  }

  &__card {
    // background-color: var(--clr-primary-200);
    // box-shadow: 1px 0px 20px var(--clr-primary-600);
    color: var(--clr-primary-200);
    padding: 2em 1.5em;
    border-radius: 0.2em;

    & + & {
      margin-top: 1em;

      @include mq(md) {
        margin-top: 0;
      }
    }

    &--orange {
      background-color: var(--clr-primary-300);
    }

    &--cyan {
      background-color: var(--clr-primary-400);
    }
  }

  &__icon {
    font-size: pxToRem(48);
  }

  &__card-title {
    font-family: var(--ff-poppins-light);
    font-size: 2rem;
    line-height: 1.2;
    margin-top: 0.5em;
    letter-spacing: -0.04em;
  }

  &__card-desc {
    font-family: var(--ff-sf-pro-text-regular);
    font-size: pxToRem(18);
    line-height: 1.2;
    margin-top: 0.5em;
    // color: var(--clr-primary-500);
  }
}

.clients {
  // TODO: Add spacing in the spacing constant
  padding-block: 1.75em;
  background: #fff;
  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-300);
    text-align: center;
  }

  &__description {
    @extend %section-description;
    color: var(--clr-primary-500);
    text-align: center;
    margin-bottom: 1em;
  }

  &__row {
    display: flex;
    flex-direction: column;

    @include mq(md) {
      flex-direction: row;
    }
  }

  &__logo {
    filter: grayscale(100%);
    transition: filter 0.5s ease-in-out;
    cursor: pointer;

    &:hover {
      filter: grayscale(0%);
    }
  }
}

.testimonials {
  padding-top: 1.5em; // TODO: Add spacing in the spacing constant
  padding-bottom: 6em; // TODO: Add spacing in the spacing constant
  position: relative;

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-400);
    text-align: center;
    margin-bottom: 0.5em;
  }

  &__button {
    background: transparent;
    border: none;
    font-size: 1em !important;
    position: absolute;
    top: 53% !important;
    cursor: pointer;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--clr-primary-400);
    }

    &--prev {
      left: 5% !important;
    }

    &--next {
      right: 5% !important;
    }
  }

  &__dot {
    bottom: 45px !important;
  }
}

.testimonial {
  background-color: var(--clr-primary-200);
  color: var(--clr-primary-100);
  padding: 1.5em;
  border-radius: 0.2em;
  box-shadow: 1px 0px 20px var(--clr-primary-600);
  border: 1px solid var(--clr-primary-300);
  display: flex;
  width: 32.4%;
  gap: 4%;

  & + & {
    margin-left: 1em;
  }

  @include mq(md) {
  }

  &__avatar {
    width: 10em;
    height: fit-content;
    border-radius: 50%;
    overflow: hidden;
    max-width: 17em;
    aspect-ratio: 1;
  }

  &__username {
    font-family: var(--ff-poppins-light);
    color: var(--clr-primary-300);
    font-size: pxToRem(15);
    line-height: 1.2;
    letter-spacing: 0.05em;
  }

  &__icon {
    // TODO: Add color in the colors palette
    color: #eacf76;
    font-size: pxToRem(12);
  }

  &__description {
    font-family: var(--ff-sf-pro-text-regular);
    font-size: pxToRem(15);
    line-height: 1.5;
    margin-top: 0.5em;
    color: var(--clr-primary-500);
  }
}

.support {
  background-color: #fff;
  // background-position-y: 5em;
  padding-top: 0.5em;
  padding-bottom: 2em;

  @include mq(md) {
    background-image: url("../assets/images/support.jpg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position-x: right;
    background-position-y: 2em;
    padding-bottom: 3.5em;
  }

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-300);
    text-align: center;
    margin-block: 0.5em;
  }

  &__img {
    margin-top: -1.3em;
    margin-bottom: -2em;

    @include mq(md) {
      display: none;
    }
  }

  &__description {
    color: var(--clr-primary-500);
    font-family: var(--ff-poppins-regular);
    font-size: pxToRem(15);
    line-height: 1.5;
    text-align: center;

    @include mq(md) {
      font-size: pxToRem(18);
      width: 45%;
      text-align: left;
    }
  }

  &__form {
    max-width: 32em;
    font-family: var(--ff-sf-pro-text-regular);
    font-size: pxToRem(16);

    margin-top: 1em;

    * {
      padding-inline: 0.5em;
      border-radius: 0.4em;
      border: 1px solid var(--clr-primary-100);
      transition: border 0.3s ease-in-out;
      color: var(--clr-primary-500);

      &:focus {
        outline: var(--clr-primary-300) solid 1px;
        border: 1px solid var(--clr-primary-300);
      }
    }

    input[type="text"],
    input[type="email"],
    input[type="number"] {
      width: 100%;
      padding-block: 0.5em;
      margin-top: 0.8em;

      @include mq(md) {
        width: 49.5%;
      }
    }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
  -webkit-appearance: none;
 
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}



    input[type="submit"] {
      color: var(--clr-primary-200);
      cursor: pointer;
    }

    textarea {
      width: 100%;
      resize: none;
      padding-block: 0.5em;
      margin-top: 0.7em;
    }
  }
}

.demo {
  padding-top: 1.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2.5em;

  &__heading {
    @extend %section-heading;
    color: var(--clr-primary-400);
    text-align: center;
    margin-bottom: 0.2em;
  }

  &__description {
    color: var(--clr-primary-500);
    font-size: pxToRem(16);
    font-family: var(--ff-poppins-regular);
    text-align: center;
    width: 80%;

    @include mq(md) {
      font-size: pxToRem(21);
      margin-top: 0.7em;
      width: 45%;
    }
  }
}

.footer {
  background-color: var(--clr-primary-100);
  padding-block: 1.875em;

  &__heading {
    font-family: var(--ff-poppins-regular);
    color: var(--clr-primary-200);
    text-align: center;
    margin-bottom: 0.4em;
  }

  &__icons-row {
    display: flex;
  }

  &__link {
    text-decoration: none;
    display: inline-block;

    & + & {
      margin-left: 1em;
    }

    &:hover .footer__social-icon {
      color: var(--clr-primary-200);
      background-color: var(--clr-primary-400);
    }
  }

  &__social-icon {
    font-size: pxToRem(18);
    color: var(--clr-primary-100);
    display: inline-block;
    background-color: var(--clr-primary-200);
    border-radius: 50%;
    width: 1.7em;
    aspect-ratio: 1;
    display: grid !important;
    place-items: center;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

    @include mq(md) {
      font-size: pxToRem(24);
    }
  }

  &__copyright {
    font-family: var(--ff-poppins-regular);
    color: var(--clr-primary-200);
    text-align: center;
    font-size: pxToRem(14);
    margin-top: 2em;

    @include mq(md) {
      margin-top: 3em;
    }
  }
}

.reveal {
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: 1s transform ease, 1s opacity ease;

  &.active {
    transform: translateY(0);
    opacity: 1;
  }
}
